import React, { FC, useCallback, useEffect, useRef } from 'react';

import './styles.css';

interface Props {
    value: any;
    selected: boolean;
    color: string;
    onChange: (value: any) => void;
    setBar: (value: { left: number, width: number }) => void;
}

/**
 * Tab component to be used inside the Tabs component;
 */
export const Tab: FC<Props> = ({ children, value, selected, color, onChange, setBar }) => {

    const ref = useRef<HTMLDivElement>(null);

    const _onClick = useCallback(() => {
        if (onChange) {
            onChange(value);
        }
    }, [value, onChange]);

    useEffect(() => {
        if (setBar && selected && ref.current) {
            setBar({ left: ref.current.offsetLeft, width: ref.current.offsetWidth });
        }
    }, [selected, setBar]);

    return <div
        ref={ref}
        className="ui-tab"
        style={{
            color: selected ? color : undefined,
            transition: selected ? 'color .2s .1s' : 'color .2s'
        }}
        onClick={_onClick}
    >{children}</div>;
}